.login_container {
  position: fixed;
  top: 0px;
  left: 0px;
  height: 100vh;
  width: 100%;
  background-color: rgba(128, 128, 128, 0.508);
  @include flex(center, center, row, 0px);
  text-decoration: none;
  color: $mainFontColor;

  &_heading {
    width: 100%;
    position: relative;
  }

  h2 {
    font-size: 30px;
    color: #fff;
    line-height: 1.2;
    text-align: center;
    background-color: $mainGreen;
    padding: 25px;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
  }

  &_input {
    font-size: 18px;
    color: $mainGreen;
    line-height: 1.2;
    width: 300px;
    height: 55px;
    background: #ebebeb;
    border-radius: 27px;
    padding: 0 25px;
    border: none;

    &::placeholder {
      color: black;
    }

    &_register {
      margin-top: 20px;
      font-size: 1rem;
      text-align: center;

      &_now {
        text-align: center;
        text-decoration: none;
        color: $mainGreen;
        cursor: pointer;
      }
    }
  }

  &_form {
    @include flex(center, center, column, 50px);
    width: 500px;
    border-radius: 25px;
    box-shadow: 0 3px 20px 0 rgba(0, 0, 0, 0.1);
    padding-bottom: 50px;
    background-color: white;
    position: relative;

    @include media(medium) {
      width: 400px;
    }
  }
  &_inputs {
    @include flex(center, center, column, 30px);
  }
}

.formError {
  border: 1px solid red;
  background-color: white;

  &::placeholder {
    color: black;
  }
}
