.notification_container {
  width: 100%;
  position: fixed;
  height: 2rem;
  display: flex;
  top: 0;
  right: 0;
  font-size: 1.5rem;
  padding: 10px 40px;
  justify-content: space-between;
  align-items: center;
  color: white;
  z-index: 999;

  h4,
  p {
    font-size: 1rem;
    margin: 0;
  }
}

.error {
  background-color: red;
}

.success {
  background-color: $mainGreen;
}

.error_notification_container {
  width: 100%;
  position: fixed;
  height: 3rem;
  display: flex;
  top: 0px;
  right: 0px;
  font-size: 1.5rem;
  padding: 10px 40px;
  justify-content: space-between;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  align-items: center;
  color: white;

  h4,
  p {
    font-size: 1rem;
    margin: 0;
  }
}

.error {
  background-color: red;
}

.success {
  background-color: $mainGreen;
}

.success-message {
  @include flex(center, center, column, 30px);
}
